.option {
    background: #3E8FCC1A;
    padding: 26px;
    color: #0F0F0F;
    font-size: 28px;
    font-weight: 300;
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover {
        background: #3E8FCC;
        color: #FFFFFF;
    }
}

.option-active {
    background: #3E8FCC;
    color: #FFFFFF;

}

.option_check {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 1px solid #0F0F0F;
    margin-right: 31px;
    
    &-active {
        border-color: #FFFFFF;
    }
}