.main {
    height: 100vh;
    background-size: cover;
}

.background {
    float: right;
    height: 100%;
    
}

.wrapper {
    max-width: 1920px;
    padding: 96px 54px 0px 54px;
    // margin: 0 auto;
}

h1 {
    font-size: 96px;
    font-weight: 700;
    color: #005392;
    line-height: 110%;
    margin-block-start: 0;
    margin-block-end: 0;
}

h2 {
    font-size: 80px;
    font-weight: 700;
    color: #005392;
    line-height: 110%;
    margin-block-start: 0;
    margin-block-end: 0;
}

.logo_block {
    width: 346px;    
}

.heading_block {
    width: 802px;
    margin-top: 98px;
}

.subheading {
    color: #333333;
    font-size: 36px;
    font-weight: 300;
    margin-top: 24px;
    margin-bottom: 37px;
}



.auth {
    height: 100vh;
    background-size: cover;
    background-position-x: -1200px;
    background-repeat: no-repeat;
}

.layout {
    display: grid;
    grid-template-columns: 1fr 2fr;
}

.auth_logo_block {
    margin-top: 65px;
}

.auth_wrapper {
    padding: 0 200px;
    // margin: 0 auto;
}

.auth_heading_block {
    margin-top: 49px;
}

.auth_input_block {
    margin-bottom: 63px;
}

.result_block {
    background: rgba(62, 143, 204, 0.18);
    border-radius: 8px;
    padding: 42px 171px;
    font-weight: 500;
font-size: 48px;
line-height: 100%;
/* identical to box height, or 48px */
color: #005392;
text-align: center;
margin-bottom: 40px;
}