.test_page {
    text-align: center;
    padding: 44px;
}

.test_heading_block {
    width: 1420px;
    margin: 0 auto;
    margin-bottom: 70px;
    
}

.test_heading {
    font-size: 48px;
    color: #005392;
    width: 1420px;    
    margin-top: 33px;
    font-weight: 700;
}

.test_tip {
    color: #747474;
    margin-top: 36px;
    font-size: 28px;
    font-weight: 300;

}

.test_options_block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 48px;
    margin-bottom: 92.5px;
}

.option_text {
    text-align: left;
}

.test_progress {
    padding: 16.5px 62px;
    background: rgba(62, 143, 204, 0.18);
    border-radius: 800px;
    font-size: 36px;
    font-weight: 300;
    color: #005392;
    

}